.projects {
    background-image: url('http://wpdemos.themezaa.com/pofo/wp-content/uploads/2017/06/testimonial-bg.png');
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;

    .container {
        width: 90%;
        height: 100%;
        display: flex;
        flex-direction: column;
        padding-top: 50px;
        margin-bottom: 70px;

        div span {
            font-weight: 600;
        }
    }

    .projectsBlock {
        height: 100%;
        display: flex;
        justify-content: space-between;
    }
}

@media screen and (max-width: 1440px) {
    .projects .container {
        width: 90%;
    }
}

@media screen and (max-width: 760px) {
    .projects .container {
        padding-top: 40px;
        margin-bottom: 40px;

        .projectsBlock {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
}
